import axiosInstance from "../plugins/axios";
import config from "./dataServiceConfig";
class RandevuDataService {
    constructor() { 
       
    }
    /*
    ilceListesi,
    ilListesi,
    kisiListesi,
    saatListesi,
    tarihListesi,
    uygunIslemTipiListesi
    */
   
    
   
    
  
    
   
    
  

    async bildirimKaydet(data) {

        let res = await axiosInstance.post(config.bildirimKaydet,data );
       
        return await res.data;
    };

    async bildirimGonder(data) {

        let res = await axiosInstance.post(config.bildirimGonder,data );
       
        return await res.data;
    };
}
let instance = new RandevuDataService();
//export {  RandevuDataService as default };
export default instance;