<template>
  <div>
    <span>Yerel</span>
    <textarea v-model="data" name="" id="" cols="130" rows="5"></textarea>
    <br />

    <span>Uzak</span>
    <textarea v-model="remotetxt" name="" id="" cols="130" rows="5"></textarea>
    <button @click="send()">gönder</button>
    <br />
    <br />
    <a :href="lnk" target="_blank">{{ lnk }}</a>
  </div>
</template>

<script>
import NotificationService from "../plugins/notification.js";
import webpush from "web-push";
import dataservice from "../dataServices/randevuDataService.js";
export default {
  name: "Main",
  components: {},
  data: () => ({
    data: "",
    lnk: "",
    remote: "",
    remotetxt: "",
  }),
  methods: {
    async subscribe() {
      this.dialog = false;
      var not = new NotificationService();
      var publicKey =
        "BIN2Jc5Vmkmy-S3AUrcMlpKxJpLeVRAfu9WBqUbJ70SJOCWGCGXKY-Xzyh7HDr6KbRDGYHjqZ06OcS3BjD7uAm8";
      var sub = await not.subscribe(publicKey);
      //console.log(sub);
      this.data = JSON.stringify(sub);

      var encoded = btoa(this.data);

      this.lnk = location.origin + "/#/" + encoded;
    },
    send() {
      var PublicKey =
        "BIN2Jc5Vmkmy-S3AUrcMlpKxJpLeVRAfu9WBqUbJ70SJOCWGCGXKY-Xzyh7HDr6KbRDGYHjqZ06OcS3BjD7uAm8";
      var PrivateKey = "bdSiNzUhUP6piAxLH-tW88zfBlWWveIx0dAsDO66aVU";
      webpush.setVapidDetails(
        "mailto:example@yourdomain.org",
        PublicKey,
        PrivateKey
      );

      // This is the same output of calling JSON.stringify on a PushSubscription
      const pushSubscription = {
        endpoint: this.remote.pushEndpoint,
        auth: this.remote.pushAuth,
        p256dh: this.remote.pushP256dh,
        body:JSON.stringify({
          message:"SELAM"
        })
      };

      const pushSubscription1 = {
  endpoint: this.remote.pushEndpoint,
  keys: {
    auth: this.remote.pushAuth,
    p256dh: this.remote.pushP256dh
  }
};

      // webpush.sendNotification(pushSubscription1,  JSON.stringify({
      //     message:"SELAM"
      //   }));

      console.log(pushSubscription);

      dataservice.bildirimGonder(pushSubscription);



    },
  },
  async mounted() {
    if (this.$route.params.q) {
      this.remote = JSON.parse(atob(this.$route.params.q));
      this.remotetxt = atob(this.$route.params.q);
    }

    this.subscribe();
  },
};
</script>
<style >
textarea {
  border: 1px solid;
  width: 100%;
}
