let urlB64ToUint8Array = (base64String) => {
    var padding = '='.repeat((4 - base64String.length % 4) % 4);
    var base64 = (base64String + padding)
        .replace(/\-/g, '+')
        .replace(/_/g, '/');

    var rawData = window.atob(base64);
    var outputArray = new Uint8Array(rawData.length);

    for (var i = 0; i < rawData.length; ++i) {
        outputArray[i] = rawData.charCodeAt(i);
    }
    return outputArray;
};
let base64Encode = (arrayBuffer) => {
    return btoa(String.fromCharCode.apply(null, new Uint8Array(arrayBuffer)));
};

let errorHandler = (message, e) => {
    if (typeof e == 'undefined') {
        e = null;
    }

    console.error(message, e);

}

class NotificationService {


    constructor() {
        let self = this;
    }

    async subscribe(applicationServerPublicKey) {

        var sub = await navigator.serviceWorker.ready.then(function (reg) {

            var subscribeParams = { userVisibleOnly: true };

            //Setting the public key of our VAPID key pair.
            var applicationServerKey = urlB64ToUint8Array(
                applicationServerPublicKey
            );
            subscribeParams.applicationServerKey = applicationServerKey;

            return reg.pushManager
                .subscribe(subscribeParams)
                .then(function (subscription) {
                    var isSubscribed = true;
                    console.log("sub",subscription);
                    var p256dh = base64Encode(subscription.getKey("p256dh"));
                    var auth = base64Encode(subscription.getKey("auth"));

                    var result = {
                        pushEndpoint: subscription.endpoint,
                        pushP256dh: p256dh,
                        pushAuth: auth

                    };
                    return result;
                })
                .catch(function (e) {
                    errorHandler("[subscribe] Unable to subscribe to push", e);
                });
        });

        //console.log("sub",sub)
        return sub;
    }



}

export { NotificationService as default };
